:root {
  --dark-bg: rgb(10, 5, 23);
  --border-color: #0084ff;
  --primary: #0084ff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#portfolio {

  background-size: 40px 40px;
  /* background-color: var(--dark-bg); */
  background-image: linear-gradient(to right, rgba(243, 245, 175, 0.12) 2px, transparent 1px),
  linear-gradient(to bottom, rgba(243, 245, 175, 0.12) 2px, transparent 1px);
}

header {
  height: 160px;
  background-color: var(--dark-bg);
  color: whitesmoke;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 3px solid var(--border-color);
}

main {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
}

#main-header {
  margin: 10px 0;
  font-size: 20px;
}

#project-list {
  display: flex;
  gap: 40px;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.project {
  border: 1px solid var(--border-color);
  width: 38%;
  padding: 5px;
}

.project-img {
  width: 100%;
  height: 350px;
  object-fit: cover;
}

.project-title {
  color: var(--primary);
}

footer {
  margin-top: 20px;
  padding: 10px 0;
  border-top: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  font-size: 30px;
  position: sticky;
  bottom: 0;
}











.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
